'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Alert, AlertDescription } from '@/components/ui/alert'
import LocationResult from './LocationResult'

interface LocationData {
  number: string
  city: string
  country: string
  latitude: number
  longitude: number
  region?: string
  timezone?: string
}

interface ApiResponse {
  success: boolean
  location?: LocationData
  error?: string
}

export default function PhoneTrackingForm() {
  const [phoneNumber, setPhoneNumber] = useState('')
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')
  const [locationData, setLocationData] = useState<LocationData | null>(null)

  const validatePhoneNumber = (number: string): boolean => {
    // Basic phone number validation - allows digits, spaces, hyphens, parentheses, and + prefix
    const phoneRegex = /^[\+]?[\d\s\-\(\)]{7,15}$/
    return phoneRegex.test(number.trim())
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    // Reset previous states
    setError('')
    setLocationData(null)
    
    // Validate input
    if (!phoneNumber.trim()) {
      setError('Please enter a phone number')
      return
    }
    
    if (!validatePhoneNumber(phoneNumber)) {
      setError('Please enter a valid phone number (7-15 digits)')
      return
    }
    
    setLoading(true)
    
    try {
      const response = await fetch('/api/phone-location', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ phoneNumber: phoneNumber.trim() }),
      })
      
      const data: ApiResponse = await response.json()
      
      if (!response.ok) {
        throw new Error(data.error || 'Failed to fetch location data')
      }
      
      if (data.success && data.location) {
        setLocationData(data.location)
      } else {
        setError('No location data found for this phone number')
      }
    } catch (err) {
      setError(err instanceof Error ? err.message : 'An unexpected error occurred')
    } finally {
      setLoading(false)
    }
  }

  const handleReset = () => {
    setPhoneNumber('')
    setLocationData(null)
    setError('')
  }

  return (
    <div className="space-y-6">
      <form onSubmit={handleSubmit} className="space-y-4">
        <div className="space-y-2">
          <Label htmlFor="phone" className="text-sm font-medium text-gray-700">
            Phone Number
          </Label>
          <Input
            id="phone"
            type="tel"
            placeholder="Enter phone number (e.g., +1234567890)"
            value={phoneNumber}
            onChange={(e) => setPhoneNumber(e.target.value)}
            className="w-full"
            disabled={loading}
          />
        </div>
        
        {error && (
          <Alert variant="destructive">
            <AlertDescription>{error}</AlertDescription>
          </Alert>
        )}
        
        <div className="flex gap-3">
          <Button 
            type="submit" 
            disabled={loading || !phoneNumber.trim()}
            className="flex-1"
          >
            {loading ? 'Tracking Location...' : 'Track Location'}
          </Button>
          
          {(locationData || error) && (
            <Button 
              type="button" 
              variant="outline" 
              onClick={handleReset}
              disabled={loading}
            >
              Reset
            </Button>
          )}
        </div>
      </form>
      
      {locationData && (
        <div className="mt-6">
          <LocationResult location={locationData} />
        </div>
      )}
    </div>
  )
}
